.widgetContainer {
    font-size: 1.2em;
    background-color: #fff;
}

.even {

}

.odd {
    background-color: #F6F6F6;
}

.btn {
    font-size: 1em;
    padding: 8px 16px;
    text-transform: uppercase;
}

.btnPrimary {
    composes: btn from '../buttons.css';
    composes: btn;
    color: #fff;

    background-color: #58bd44;
    border-color: #58bd44;
}

.btnDanger {
    composes: btn from '../buttons.css';
    composes: btn;
    color: #fff;
    background-color: #ff4c4c;
    border-color: #ff4c4c;
}

.btnUp {
    composes: btnPrimary;
}

.btnUp:before {
    content: url(data:image/gif;base64,R0lGODlhCAAMAIABAP///////yH5BAEAAAEALAAAAAAIAAwAAAIUjAGmiMv9YoLpVBUaztdt/mmYWAAAOw==);
    margin-right: 5px;
}

.btnDown {
    composes: btnDanger;
}
.btnDown:before {
    content: url(data:image/gif;base64,R0lGODlhCAAMAIABAP///////yH5BAEAAAEALAAAAAAIAAwAAAITjAGmgXvpWoRLVtoy2pxxvE1KAQA7);
    margin-right: 5px;
}

.colorPositive {
    composes: green50 from '../colors.css';
}

.colorNegative {
    composes: red50 from '../colors.css';
}

.table {
    composes: table from '../table.css';
}

.tableRow {
    composes: row from '../table.css';
}



