.widgetContainer {
    color: #f0f2f4;
    font-size: 1.2em;
    background-color: #242526;
}

.even {

}

.odd {

}

.btn {
    font-size: 1em;
    padding: 8px 16px;
    text-transform: uppercase;
}

.btnPrimary {
    composes: btn from '../buttons.css';
    composes: btn;
    composes: white from '../colors.css';
    background-color: #58bd44;
    border-color: #58bd44;
}

.btnDanger {
    composes: btn from '../buttons.css';
    composes: btn;
    composes: white from '../colors.css';
    background-color: #ff4c4c;
    border-color: #ff4c4c;
}

.btnUp {
    composes: btnPrimary;
}

.btnUp:before {
    content: url(data:image/gif;base64,R0lGODlhCAAMAIABAP///////yH5BAEAAAEALAAAAAAIAAwAAAIUjAGmiMv9YoLpVBUaztdt/mmYWAAAOw==);
    margin-right: 5px;
}

.btnDown {
    composes: btnDanger;
}
.btnDown:before {
    content: url(data:image/gif;base64,R0lGODlhCAAMAIABAP///////yH5BAEAAAEALAAAAAAIAAwAAAITjAGmgXvpWoRLVtoy2pxxvE1KAQA7);
    margin-right: 5px;
}

.colorPositive {
    composes: green100 from '../colors.css';
}

.colorNegative {
    composes: red100 from '../colors.css';
}

.table {
    composes: table from '../table.css';
}

.tableRow {
    composes: row from '../table.css';
    border-bottom: 1px solid #3a3b3c;
}


